.home_container {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  background: #030712;
  position: relative;
  .center {
    width: 1160px;
    margin: auto;
  }
  .part_title {
    font-size: 28px;
    color: #fff;
    margin-bottom: 80px;
    text-align: center;
  }
  .tit_desc {
    .tit {
      font-size: 20px;
      color: #ffffff;
      margin-bottom: 28px;
    }
    .desc {
      width: 100%;
      font-size: 16px;
      line-height: 26px;
      color: #94a4ad;
    }
  }
  .header_banner {
    width: 100%;
    background-size: cover;
    min-height: 680px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .img_bg {
      position: absolute;
      top: 0px;
      left: 0;
      width: 100%;
    }
    .header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 48px 0 0;
      width: 92%;
      margin: auto;
      z-index: 9999;
      .pc_logo {
        width: 220px;
        height: 42px;
      }
      .social_box {
        display: flex;
        align-items: center;
        display: flex;
        align-items: center;
        height: 36px;
        padding: 8px;
        border-radius: 12px;
        img {
          width: 20px;
          height: 20px;
        }
        a {
          display: flex;
          align-items: center;
          height: 36px;
          padding: 8px;
          background: rgba(#a6deeb, 0.1);
          border-radius: 12px;
          margin-left: 8px;
          &:nth-child(1){
            margin-left: 0;
          }
          &:hover {
            .socialDefault {
              display: none;
            }
            .socialHover {
              display: block;
            }
          }
        }
        .socialDefault {
          display: block;
        }
        .socialHover {
          display: none;
        }
      }
    }
    .banner{
      width: 80%;
      height: 600px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      margin-bottom: 40px;
      .title_btn{
        width: 58%;
        z-index: 9;
        .banner_text{
          font-size: 32px;
          color: #fff;
          line-height: 52px;
          text-transform: uppercase;
          text-transform: none;
          z-index: 99;
          margin-top: -100px;
          span {
            color: #a6deeb;
          }
        }
        .h5_text{
          display: none;
        }
        .btn_box{
          height: 44px;
          margin-top: 40px;
          display: flex;
          .btn {
            height: 60px;
            font-size: 18px;
            color: #0c0b1e;
            line-height: 60px;
            text-align: center;
            cursor: pointer;
            border-radius: 12px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            img {
              width: 20px;
              height: 20px;
              margin-left: 8px;
              transition: all 0.5s;
            }
            .icon_open{
              transform: rotate(180deg);
            }
          }
          .dapps{
            width: 188px;
            border-radius: 12px;
            background: #a6deeb;
            position: relative;
          }
          .documents {
            width: 236px;
            height: 60px;
            border-radius: 12px;
            border: 1px solid #ffffff;
            margin-left: 28px;
            position: relative;
            font-size: 18px;
            color: #ffffff;
          }
          .open_list{
            position: absolute;
            top: 68px;
            left: 0;
            padding: 8px;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            border-radius: 12px;
            a {
              width: 100%;
              color: #ffffff;
              height: 54px;
              line-height: 54px;
              padding-left: 8px;
              border-radius: 12px;
              display: flex;
              align-items: center;
              &:hover {
                background: rgba(#a6deeb, 0.1);
              }
              img {
                width: 20px;
                height: 20px;
                margin-right: 12px;
              }
            }
          }
          .dappsList{
            border: 1px solid rgba(#a6deeb, 0.5);
          }
          .documentsList{
            border: 1px solid rgba(255, 255, 255, 0.5);
          }
          .developerDoc {
            &:hover {
              .developer {
                display: none;
              }
              .coming {
                display: block;
              }
            }
            .coming {
              display: none;
            }
          }
        }
      }
      .banner_img_box {
        width: 30%;
        position: relative;
        scale: 0.9;
        .banner_img {
          position: absolute;
          top: -80px;
          left: 0;
          .banner_mian {
            height: 750px;
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            /* animation: move_main 5s linear infinite; */
          }
          .img_shootingstar1 {
            z-index: 9;
            width: 224px;
            height: 224px;
            position: absolute;
            top: 0px;
            left: -450px;
            animation: meteorFall1 2s ease-in-out infinite,
              delayed-animation 3s linear infinite;
          }
          .img_shootingstar2 {
            z-index: 9;
            width: 130px;
            height: 130px;
            position: absolute;
            top: -250px;
            left: -350px;
            animation: meteorFall2 2s ease-in-out infinite,
              delayed-animation 4s linear infinite;
            animation-delay: 1s;
          }
          .img_shootingstar4 {
            z-index: 9;
            width: 129px;
            height: 129px;
            position: absolute;
            top: -100px;
            right: 0;
            animation: meteorFall4 2s ease-in-out infinite,
              delayed-animation 4s linear infinite;
          }
          .img_shootingstar5 {
            z-index: 9;
            width: 61px;
            height: 61px;
            position: absolute;
            top: -250px;
            left: 0;
            animation: meteorFall5 2s ease-in-out infinite,
              delayed-animation 4s linear infinite;
            animation-delay: 1s;
          }
        }
      }
    }
  }
  .tradable_value{
    margin-top: 80px;
    margin-bottom: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: hidden;
    .center{
      position: relative;
    }
    .bg_halo1 {
      position: absolute;
      top: 100px;
      right: -180px;
      width: 50%;
    }
    .img_feather {
      position: absolute;
      top: -0px;
      right: -180px;
      width: 300px;
    }
    .content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .left {
        width: 400px;
        position: relative;
        img {
          width: 100%;
        }
        .dynamic_proof1 {
          width: 200px;
          height: 200px;
          position: absolute;
          top: 120px;
          left: -30px;
          animation: movedynamic_proof1 3s linear infinite,
            delayed-animation 2s linear infinite;
        }
        .dynamic_proof2 {
          width: 158px;
          height: 158px;
          position: absolute;
          top: 100px;
          right: -50px;
          animation: movedynamic_proof2 3s linear infinite,
            delayed-animation 4s linear infinite;
          animation-delay: 1.5s;
        }
      }
      .right {
        width: 52%;
        z-index: 999;
        .tit_desc {
          width: 100%;
          margin-top: 40px;
          border-radius: 28px;
          padding: 60px 20px 60px;
          background: linear-gradient(
            270deg,
            #06182a 0%,
            #030712 50%,
            #06070a 100%
          );
        }
      }
    }
  }
  .aggregator_layer{
    margin-bottom: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: hidden;
    .center{
      position: relative;
    }
    .bg_halo2 {
      position: absolute;
      top: 100px;
      left: -180px;
      width: 50%;
      // z-index: -1;
    }
    .img_feather {
      position: absolute;
      bottom: -100px;
      left: -150px;
      width: 300px;
      transform: rotate(270deg);
      transform: scaleX(-1) scaleY(1);
      // z-index: -1;
    }
    .item {
      width: 100%;
      background: linear-gradient(
        270deg,
        #06070a 0%,
        #030712 50%,
        #06182a 100%
      );
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px 0 20px 60px;
      margin-bottom: 40px;
      .tit_desc {
        width: 52%;
        z-index: 9999;
      }
      .img_box {
        width: 400px;
        height: 100px;
        margin-top: 100px;
        position: relative;
        img {
          position: absolute;
          top: -200px;
          right: 0;
          width: 100%;
        }
        .dynamic_multi1 {
          width: 128px;
          height: 128px;
          position: absolute;
          top: -60px;
          left: -20px;
          animation: movedynamic_multi1 3s linear infinite,
            delayed-animation 4s linear infinite;
        }
        .dynamic_multi2 {
          width: 160px;
          height: 160px;
          position: absolute;
          top: -30px;
          right: -15px;
          animation: movedynamic_multi2 3s linear infinite;
          animation-delay: 1.5s;
        }
        .dynamic_ibc1 {
          width: 90px;
          height: 95px;
          position: absolute;
          top: -100px;
          right: 60px;
          animation: movedynamic_ibc1 3s linear infinite,
            delayed-animation 6s linear infinite;
        }
        .dynamic_ibc2 {
          width: 100px;
          height: 100px;
          position: absolute;
          top: -10px;
          left: 130px;
          animation: movedynamic_ibc2 3s linear infinite;
        }
      }
    }
  }
  .derivatives {
    margin-bottom: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .content {
      display: flex;
      align-items: center;
      .item {
        width: 33%;
        background: linear-gradient(
          360deg,
          #06070a 0%,
          #030712 50%,
          #06182a 100%
        );
        border-radius: 28px;
        margin-right: 24px;
        &:nth-last-child(1) {
          margin-right: 0;
        }
        .img_box {
          img {
            width: 100%;
          }
        }
        .tit_text {
          padding: 24px;
          height: 264px;
          .tit {
            margin-bottom: 28px;
            font-size: 18px;
            color: #fff;
          }
          .text {
            font-size: 16px;
            line-height: 26px;
            color: #94a4ad;
          }
        }
      }
    }
  }
  .built_tucana{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    .img_tucana_text {
      position: absolute;
      top: 600px;
      left: 50%;
      transform: translateX(-50%);
      width: 1000px;
      z-index: 9;
    }
    .content{
      width: 600px; /* 根据需要调整容器大小 */
      height: 600px; /* 根据需要调整容器大小 */
      margin: auto;
      border: 30px solid rgba(71, 151, 175, 0.1);
      border-radius: 50%;
      margin-top: 60px;
      margin-bottom: -150px;
      position: relative;
      z-index: 99;
      .h5_item{
        display: none;
      }
      .item{
        .item_img{
          width: 600px;
          height: 600px;
          z-index: -1;
          transition: all 0.5s;
        }
        &:nth-child(1){
          position: absolute;
          top: 0;
          left: 175px;
          .item_img{
            position: absolute;
            top: 0;
            left: -192px;
          }
          .circle{
            border: 8px solid rgba(#a6deeb, 0.6);
          }
        }
        &:nth-child(2){
          position: absolute;
          top: 50%;
          right: -15px;
          .circle{
            border: 8px solid rgba(#4d51c9, 0.6);
          }
          .item_img{
            position: absolute;
            top: -300px;
            right: -33px;
          }
        }
        &:nth-child(3){
          position: absolute;
          bottom: 20%;
          left: 35px;
          .circle{
            border: 8px solid rgba(#6b4dc9, 0.6);
          }
          .item_img{
            position: absolute;
            bottom: -150px;
            left: -42px;
          }
        }
        .circle_text{
          position: relative;
          cursor: pointer;
          &:hover{
            .circle_box .circle {
              opacity: 1;
            }
            .circle::before {
              content: ""; /* 必须有 content 属性 */
              position: absolute; /* 将伪元素定位在 <p> 元素的内部 */
              top: -4px; /* 调整伪元素的位置以覆盖到 <p> 元素的边框上 */
              left: -4px; /* 调整伪元素的位置以覆盖到 <p> 元素的边框上 */
              width: calc(
                100% + 8px
              ); /* 将伪元素的宽度设置为比 <p> 元素的宽度和边框加起来更大一点 */
              height: calc(
                100% + 8px
              ); /* 将伪元素的高度设置为比 <p> 元素的高度和边框加起来更大一点 */
              background: inherit; /* 让伪元素的背景和 <p> 元素一致 */
              border-radius: inherit; /* 让伪元素的边框半径和 <p> 元素一致 */
              filter: blur(10px); /* 应用 10px 的模糊效果 */
              z-index: -1; /* 将伪元素放置在 <p> 元素的下方 */
              opacity: 1;
              animation: scale_animation 1.5s ease-in-out infinite;
            }
          }
          .circle_box{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            width: 60px;
            height: 60px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content:center;
            .circle{
              width: 28px;
              height: 28px;
              background: #fff;
              border-radius: 50%;
              opacity: 0.6;
              animation: scale_animation 1.5s ease-in-out infinite;
              transition: all 0.5s;
            }
          }
          .text{
            border-radius: 24px;
            background: rgba(255, 255, 255, 0.05);
            backdrop-filter: blur(20px);
            padding: 20px;
            position: absolute;
            left: 50%;
            top: 25px;
            transform: translateX(-50%);
            .tit{
              text-align: center;
              color: #fff;
              white-space: nowrap;
            }
            .desc {
              width: 600px;
              font-size: 14px;
              color: #fff;
              margin-top: 12px;
              text-align: left;
              white-space: normal;
            }
          }
        }
      }
    }
    .jianbian {
      position: absolute;
      top: 348px;
      left: 0%;
      width: 100%;
      height: 320px;
      background: linear-gradient(180deg, #060912 0%, #0c0a28 100%);
    }
    video {
      width: 100%;
      mix-blend-mode: screen;
      margin-top: 100px;
    }
  }
  .bottom_block{
    width: 100%;
    background: #141414;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 0;
    .pc_logo {
      width: 220px;
      height: 42px;
    }
    .social_box {
      display: flex;
      align-items: center;
      display: flex;
      align-items: center;
      margin: 40px 0;
      img {
        width: 28px;
        height: 28px;
      }
      a {
        display: flex;
        align-items: center;
        height: 36px;
        padding: 8px;
        border-radius: 12px;
        margin-left: 8px;
        &:nth-child(1){
          margin-left: 0;
        }
        &:hover {
          .socialDefault {
            display: none;
          }
          .socialHover {
            display: block;
          }
        }
      }
      .socialDefault {
        display: block;
      }
      .socialHover {
        display: none;
      }
    }
    .link_box {
      width: 80%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      a {
        font-size: 16px;
        color: #798993;
        font-weight: 400;
        height: 80px;
        margin-bottom: 20px;
        transition: all 0.5s;
        &:hover {
          scale: 1.1;
          color: #fff;
          .copyContact,
          .download {
            scale: 1;
          }
        }
      }
      .copyContact,
      .download {
        position: absolute;
        top: 30px;
        left: 50%;
        transform: translateX(-50%);
        width: 140px;
        height: 50px;
        background: rgba(255, 255, 255, 0.05);
        border-radius: 32px;
        backdrop-filter: blur(10px);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: #fff;
        display: none;
        &:hover {
          opacity: 0.7;
        }
        img {
          width: 20px;
          height: 20px;
          margin-right: 8px;
        }
      }
      .copyContact {
        width: 240px;
        img {
          margin-left: 8px;
        }
      }
      .media_kit {
        position: relative;
        &:hover {
          .download {
            display: flex;
          }
        }
      }
      .contact {
        position: relative;
        &:hover {
          .copyContact {
            display: flex;
          }
        }
      }
    }
    .copyright {
      width: 80%;
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      padding-top: 80px;
      font-size: 12px;
      color: #ffffff;
      opacity: 0.8;
      line-height: 12px;
      text-align: center;
      font-weight: 400;
      padding-bottom: 42px;
    }
  }
}

@media screen and (min-width: 1920px) {
  .home_container {
    .header_banner {
      .banner {
        .title_btn {
          width: 50%;
          .banner_text {
            font-size: 40px;
            line-height: 60px;
          }
          .btn_box {
            margin-top: 60px;
          }
        }
        .banner_img_box {
          scale: 1.2;
        }
      }
    }
    .center {
      width: 1400px;
    }
    .part_title {
      font-size: 32px;
    }
    .tit_desc {
      .tit {
        font-size: 20px;
        margin-bottom: 40px;
      }
      .desc {
        width: 100%;
        font-size: 16px;
      }
    }
    .tradable_value {
      .content {
        .left {
          width: 500px;
          img {
            width: 100%;
          }
        }
      }
    }
    .aggregator_layer {
      .item {
        .img_box {
          width: 450px;
          img {
            top: -220px;
          }
          .dynamic_ibc1 {
            right: 80px;
          }
          .dynamic_ibc2 {
            left: 155px;
            animation: movedynamic_ibc21920 3s linear infinite;
          }
        }
      }
    }
    .derivatives {
      .content {
        justify-content: space-between;
        .item {
          .tit_text {
            .tit {
              font-size: 20px;
            }
            .text {
              font-size: 16px;
            }
          }
        }
      }
    }
    .built_tucana {
      .content{
        .item{
          .circle_text{
            .text{
              .desc{
                width: 700px;
              }
            }
          }
        }
      }
      .jianbian {
        top: 353px;
      }
    }
  }
}
@media screen and (max-width: 1250px) {
  .pc_text{
    display: none !important;
  }
  .h5_text{
    display: block !important;
  }
}
@media screen and (max-width: 1200px) {
  .home_container {
    .center {
      width: 100%;
      padding: 0 40px;
    }
    .header_banner {
      min-height: unset;
      .banner {
        height: 500px;
        .title_btn {
          .banner_text {
            font-size: 28px;
            line-height: 42px;
          }
          .btn_box {
            margin-top: 30px;
          }
        }
        .banner_img_box {
          scale: 0.7;
        }
      }
    }
    .part_title {
      font-size: 24px;
    }
    .tit_desc {
      .tit {
        font-size: 16px;
      }
      .desc {
        font-size: 16px;
      }
    }
    .derivatives {
      .content {
        .item {
          .tit_text {
            .tit {
              margin-bottom: 20px;
            }
          }
        }
      }
    }
    .tradable_value {
      .bg_halo1{
        top: 160px;
        right: -30px;
      }
      .img_feather{
        right: -100px;
      }
    }
    .aggregator_layer {
      .bg_halo1{
        left: -30px;
      }
      .img_feather{
        bottom: 0px;
        left: -150px;
      }
      .item {
        padding-right: 0;
      }
      .img_box {
        scale: 0.8;
      }
    }
    .built_tucana {
      .img_tucana_text {
        width: 90%;
      }
      .content{
        .item{
          &:nth-child(2) {
            .text {
              top: -30px;
              left: 100%;
              transform: translateX(-108%);
            }
          }
          .circle_text{
            .text{
              .desc{
                width: 300px;
              }
            }
          }
        }
      }
      .jianbian {
        top: 343px;
      }
    }
    .bottom_block {
      .social_box {
        img {
          width: 24px;
          height: 24px;
        }
      }
      .link_box {
        a {
          font-size: 16px;
          height: 60px;
        }
        .copyContact,
        .download {
          font-size: 16px;
          width: 140px;
          height: 40px;
          border-radius: 24px;
          top: 24px;
          img {
            width: 16px;
            height: 16px;
          }
        }
        .copyContact {
          width: 220px;
        }
      }
    }
    .copyright {
      padding-top: 36px;
    }
  }
}

@media screen and (max-width: 960px) {
  .home_container {
    .header_banner{
      .banner{
        .btn_box{
          .open_list{
            font-size: 14px;
          }
        }
      }
    }
    .tradable_value{
      .content{
        .left{
          transform: scale(0.7);
        }
      }
    }
    .aggregator_layer {
      .item {
        padding: 30px 0 30px 60px;
        .img_box {
          width: 400px;
          scale: 0.75;
          .dynamic_multi1 {
            margin-top: -3px;
          }
          .dynamic_ibc1 {
            margin-left: 0px;
            margin-top: -5px;
          }
          .dynamic_ibc2 {
            margin-top: 0px;
